'use client';

import { useState, useMemo, useEffect, useCallback } from 'react';
import { getProducts, getUniqueProductCategories } from '@/lib/products';
import type { Product } from '@/types/product';
import { useSearchParams, useRouter, usePathname } from 'next/navigation';
import ErrorBoundary from '@/components/ErrorBoundary';
import ProductList from '@/components/products/ProductList';
import { Skeleton } from '@/components/ui/skeleton';
import { Button } from '@/components/ui/button';
import { X } from 'lucide-react';

function ProductsPageContent() {
  const router = useRouter();
  const pathname = usePathname();
  const searchParams = useSearchParams();
  const [products, setProducts] = useState<Product[]>([]);
  const [filteredProducts, setFilteredProducts] = useState<Product[]>([]);
  const [selectedProduct, setSelectedProduct] = useState<Product | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [searchQuery, setSearchQuery] = useState('');
  const [activeFilters, setActiveFilters] = useState<Record<string, string[]>>({});

  // Fetch products with pagination
  const [page, setPage] = useState(1);
  const [hasMore, setHasMore] = useState(true);
  const PAGE_SIZE = 20;

  const fetchProducts = useCallback(async (pageNum: number) => {
    try {
      setIsLoading(true);
      const data = await getProducts();
      
      // Simulate pagination for demo purposes
      // In a real app, you would pass pagination params to your API
      const paginatedData = data.slice(0, pageNum * PAGE_SIZE);
      
      setProducts(paginatedData);
      setFilteredProducts(paginatedData);
      setHasMore(paginatedData.length < data.length);
    } catch (error) {
      console.error('Error fetching products:', error);
    } finally {
      setIsLoading(false);
    }
  }, []);

  // Initial load
  useEffect(() => {
    fetchProducts(1);
  }, [fetchProducts]);

  // Load more products
  const loadMore = useCallback(() => {
    if (!isLoading && hasMore) {
      fetchProducts(page + 1);
      setPage(prev => prev + 1);
    }
  }, [fetchProducts, hasMore, isLoading, page]);

  // Handle URL search params for product selection
  useEffect(() => {
    const productId = searchParams.get('product');
    if (productId && products.length > 0) {
      const product = products.find(p => p.sku === productId);
      if (product) {
        setSelectedProduct(product);
      }
    } else {
      setSelectedProduct(null);
    }
  }, [searchParams, products]);

  // Filter products based on search query and active filters
  useEffect(() => {
    let result = [...products];

    // Apply search query
    if (searchQuery) {
      const query = searchQuery.toLowerCase();
      result = result.filter(product => 
        (product.description?.toLowerCase().includes(query)) ||
        (product.sku?.toLowerCase().includes(query)) ||
        (product.product_category?.toLowerCase().includes(query))
      );
    }

    // Apply filters
    if (Object.keys(activeFilters).length > 0) {
      result = result.filter(product => {
        return Object.entries(activeFilters).every(([type, values]) => {
          if (values.length === 0) return true;
          
          switch (type) {
            case 'category':
              return values.includes(product.product_category || '');
            case 'pressure':
              return values.some(v => {
                const [min, max] = v.split('-').map(Number);
                return product.pressure_min_bar === min && product.pressure_max_bar === max;
              });
            case 'power':
              return values.some(v => {
                const [power, unit] = v.split(' ');
                return (unit === 'HP' && product.power_hp === Number(power)) || 
                       (unit === 'kW' && product.power_kw === Number(power));
              });
            case 'voltage':
              return values.some(v => 
                product.voltage_v === Number(v.replace('V', ''))
              );
            case 'connection':
              return values.some(v => 
                product.connection_types?.includes(v)
              );
            case 'size':
              return values.some(v => 
                product.dimensions_mm_list?.includes(Number(v.replace('mm', '')))
              );
            case 'weight':
              return values.some(v => 
                product.weight_kg === Number(v.replace(' kg', ''))
              );
            default:
              return true;
          }
        });
      });
    }

    setFilteredProducts(result);
  }, [products, searchQuery, activeFilters]);
  
  // Group filtered products by category
  const productsByCategory = useMemo(() => {
    const groups: { [key: string]: Product[] } = {};
    
    filteredProducts.forEach((product) => {
      const category = product.product_category || 'Uncategorized';
      if (!groups[category]) {
        groups[category] = [];
      }
      groups[category].push(product);
    });
    
    return groups;
  }, [filteredProducts]);

  const handleProductSelect = useCallback((product: Product) => {
    const params = new URLSearchParams(searchParams);
    params.set('product', product.sku);
    router.push(`${pathname}?${params.toString()}`, { scroll: false });
  }, [pathname, router, searchParams]);

  const closeProductDetails = useCallback(() => {
    const params = new URLSearchParams(searchParams);
    params.delete('product');
    router.push(`${pathname}?${params.toString()}`, { scroll: false });
  }, [pathname, router, searchParams]);

  const handleBackToList = () => {
    const params = new URLSearchParams(searchParams.toString());
    params.delete('product');
    router.push(`${pathname}?${params.toString()}`, { scroll: false });
  };

  return (
    <div className="bg-gray-50 min-h-screen">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="flex flex-col lg:flex-row gap-8">
          {/* Main content area */}
          <div className="flex-1">
            <ErrorBoundary>
              {isLoading && products.length === 0 ? (
                <div className="container mx-auto px-4 py-8">
                  <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
                    {[...Array(8)].map((_, i) => (
                      <div key={i} className="space-y-4">
                        <Skeleton className="h-48 w-full" />
                        <Skeleton className="h-4 w-3/4" />
                        <Skeleton className="h-4 w-1/2" />
                      </div>
                    ))}
                  </div>
                </div>
              ) : filteredProducts.length === 0 ? (
                <div className="bg-white rounded-lg shadow p-8 text-center">
                  <p className="text-gray-500">
                    {searchQuery || Object.keys(activeFilters).length > 0
                      ? 'No products match your search criteria. Try adjusting your filters.'
                      : 'There are currently no products available.'}
                  </p>
                </div>
              ) : (
                <ProductList
                  products={filteredProducts}
                  loading={isLoading}
                  hasMore={hasMore}
                  onLoadMore={loadMore}
                  renderProduct={renderProduct}
                  className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6"
                />
              )}
            </ErrorBoundary>
          </div>
        </div>
      </div>

      {/* Product Details Side Panel */}
      {selectedProduct && (
        <div className="fixed inset-y-0 right-0 w-full max-w-md bg-white shadow-xl z-50 transform transition-transform duration-300 ease-in-out translate-x-0">
          <div className="h-full flex flex-col">
            <div className="flex items-center justify-between px-6 py-4 border-b">
              <h2 className="text-lg font-medium">Product Details</h2>
              <button
                onClick={closeProductDetails}
                className="text-gray-400 hover:text-gray-500"
                aria-label="Close panel"
              >
                <X className="h-6 w-6" />
              </button>
            </div>
            <div className="flex-1 overflow-y-auto p-6">
              <ErrorBoundary>
                <ProductDetailsCard product={selectedProduct} />
              </ErrorBoundary>
            </div>
            <div className="border-t px-6 py-4">
              <Button 
                onClick={closeProductDetails}
                className="w-full"
                variant="outline"
              >
                Close
              </Button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
